<template>
  <div
    class="home"
    :class="{
      small: screenWidth < 480,
      min: screenWidth < 700,
      med: screenWidth < 1200,
    }"
  >
    <!-- 页头部分 -->
    <div class="header">
      <div class="title">
        <!-- 头部文字信息 -->
        <span class="name">次顶级</span>
        <p>
          <span class="describe">了解真相，才能获得真正的自由！</span>
          <span class="webUrl" @click="copyUrL"
            >复制地址：https://subtoph.github.io</span
          >
        </p>

        <!-- 大屏幕导航 -->
        <div class="nav" v-if="screenWidth > 800">
          <p
            v-for="item in navList"
            :class="{ act: actNavIndex == item.naviIndex }"
            :key="item.index"
            @click="changeNavAct(item.naviIndex)"
          >
            {{ item.name }}
          </p>
        </div>
        <!-- 移动端导航 -->
        <div class="nav" v-else>
          <span
            class="iconfont icon-24px"
            @click="showNavlist = !showNavlist"
          ></span>
        </div>
        <!-- 移动端导航 -->
        <div class="navlist" v-if="showNavlist && screenWidth < 800">
          <p
            v-for="item in navList"
            :class="{ act: actNavIndex == item.naviIndex }"
            :key="item.index"
            @click="changeNavAct(item.naviIndex)"
          >
            {{ item.name }}
          </p>
        </div>
      </div>
    </div>
    <!-- 底部内容部分，包括导航和内容 -->
    <div class="contentBox">
      <!-- 内容部分 -->
      <div class="content">
        <router-view />
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Home",
  data() {
    return {
      showNavlist: false, //移动端导航切换
      screenWidth: "", //页面宽度
      actNavIndex: "", //当前选中的导航
      navList: [
        //多层级导航数据
        {
          name: "学习笔记",
          naviIndex: "notes",
        },
        {
          name: "个人简介",
          naviIndex: "about",
        },
      ],
    };
  },
  created() {
    // 获取路由导航参数，刷新页面保持导航不变
    this.actNavIndex = window.location.href.split("#/")[1];
  },
  mounted() {
    this.monitorWidth(); //监听页面宽度
  },
  methods: {
    // 导航变化传入naviIndex
    changeNavAct(naviIndex) {
      this.actNavIndex = naviIndex; //当前选中页面
      this.$router.push({ path: naviIndex }); //跳转对应页面
    },
    // 监听页面宽度
    monitorWidth() {
      //初始化页面宽度
      this.screenWidth = document.body.clientWidth;
      window.addEventListener("resize", () => {
        //页面宽度变化继续监听
        this.screenWidth = document.body.clientWidth;
      });
    },
    //复制网站地址
    copyUrL() {
      // 创建一个input元素
      const input = document.createElement("input");
      // 添加到body中
      document.body.appendChild(input);
      // 设置input的值
      input.setAttribute("value", "https://subtoph.github.io");
      // 选择文本域内容
      input.select();
      // 执行浏览器复制命令
      // 复制命令会将当前选中的内容复制到剪切板中（这里就是创建的input标签）
      // Input要在正常的编辑状态下原生复制方法才会生效
      if (document.execCommand("copy")) {
        document.execCommand("copy");
      }
      alert("https://subtoph.github.io复制成功"); //提示
      // 清除元素
      document.body.removeChild(input);
    },
  },
};
</script>
<style lang="less" scoped>
.home {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgb(233, 233, 233);
  .header {
    position: fixed;
    height: 40px;
    background: #fff;
    width: 100%;
    z-index: 99;
    .title {
      width: 80%;
      line-height: 40px;
      margin: 0 auto;
      padding: 0 20px;
      box-sizing: border-box;
      overflow: hidden;
      span {
        display: block;
        float: left;
      }
      p {
        margin: 0;
        float: left;
        margin-left: 30px;
        span {
          float: none;
          line-height: 20px;
          color: #aaa;
          cursor: pointer;
        }
      }
      .name {
        font-weight: 700;
        font-size: 24px;
        font-style: italic;
      }
      .nav {
        float: right;
        font-size: 18px;
        cursor: pointer;
        p {
          margin: 0;
          padding: 0 10px;
          transition: 0.3s;
          &.act {
            color: rgb(40, 140, 255);
            transform: scale(1.1);
          }
        }
        .iconfont {
          font-size: 24px;
        }
      }
      .navlist {
        position: absolute;
        right: 0;
        top: 45px;
        width: 100px;
        background: #fff;
        border-radius: 5px;
        box-shadow: 0px 1px 4px 0px rgb(146, 146, 146);
        p {
          float: none;
          text-align: center;
          cursor: pointer;
          &.act {
            color: rgb(40, 140, 255);
            transform: scale(1.1);
          }
        }
      }
    }
  }
  .contentBox {
    position: absolute;
    background: rgb(0, 63, 136);
    height: 100%;
    width: 100%;
    padding-top: 50px;
    padding-bottom: 10px;
    display: flex;
    box-sizing: border-box;
    transition: 0.3s;
    .content {
      position: relative;
      margin: 0 auto;
      width: 80%;
      background: #fff;
      border-radius: 5px;
      padding: 10px 3px;
      box-sizing: border-box;
    }
  }
}
// 名字
.home.min .header .name {
  font-size: 16px;
}
// 标题整体
.home.med .header .title {
  width: 100%;
  padding: 0 5px;
}
.min .contentBox {
  padding-top: 45px;
  padding-bottom: 5px;
}
// 内容盒子
.home.med .contentBox .content {
  width: 100%;
}
.med .header .title p {
  margin: 0;
}
.min .header .title p {
  font-size: 12px;
}
// 名字隐藏
.min .header .title .name {
  display: none;
}
</style>
